<template>
  <div class="object-modal">
    <a-modal
      :title="title"
      :visible="objectVisible"
      @cancel="handleCancel"
      cancelText=""
      okText=""
      :mask="false"
      :maskClosable="false"
      :width="600"
      class="modalCls"
    >
      <a-form-model ref="objectForm" :model="objectForm" :rules="objectFormRule">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="objectName">
              <a-input v-model="objectForm.objectName" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="code">
              <a-input v-model="objectForm.code" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="objectType">
                <a-select v-model="objectForm.objectType" placeholder="" allowClear>
                  <a-select-option value="email"></a-select-option>
                  <a-select-option value="web">web</a-select-option>
                </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="description">
              <a-textarea v-model="objectForm.description" placeholder="" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
          <a-button type="link" ghost @click="handleCancel"></a-button>
          <a-button type="primary" @click="handleOk"></a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
export default {
  name: "object-modal",
  props: {
    // 
    objectVisible: {
      type: Boolean,
      default: false,
      required: true,
    },
    // 
    title: {
      type: String,
      default: "/",
    },
    //  
    content: {
      type: Object,
      default: function () {
        return {
          objectName: "",
          objectType: "",
          description: "",
          creator: "",
          gmtCreate: "",
          gmtModified: "",
        };
      },
    },
    //  0 1
    optType:{
      type:Number,
      default:0,
      required: true,
    }
  },
  data() {
    return {
      objectFormRule: {
        objectName: [{ required: true, message: "",trigger:'blur' }],
        objectType: [{ required: true, message: "",trigger:'change' }],
        code: [{ required: true, message: "",trigger:'blur' }],
      },
      objectForm:{
          objectName: "",
          objectType: "",
          code: "",
          description: "",
      }
    };
  },
  methods: {
    handleOk() {
        this.$refs.objectForm.validate(valid => {
            if(valid){
                console.log(this.objectForm)
                this.$emit("objectOk", {formContent:this.objectForm,optType:this.optType});
            } else {
                this.$message.warning('');
                return false
            }
        })
    },
    handleCancel() {
      this.$emit("objectCancel", "");
    },
  },
  watch:{
    content:{
      handler:function(newVal){
        Object.assign(this.objectForm,newVal);
      },
      deep:true
    }
  }
};
</script>
