<template>
  <div class="ruleGroup-modal">
    <a-modal
        :title="title"
        :visible="ruleVisible"
        @cancel="handleCancel"
        cancelText=""
        okText=""
        :mask="false"
        :maskClosable="false"
        :width="600"
        class="modalCls"
    >
      <a-form-model ref="ruleForm" :model="ruleForm" :rules="ruleFormRule">
        <a-row :gutter="[24,24]">
          <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="ruleGroupId">
              <a-select v-model="ruleForm.ruleGroupId" placeholder="" allowClear>
                <a-select-option
                    :value="ruleGroup.id"
                    v-for="ruleGroup of ruleGroupData"
                    :key="ruleGroup.id"
                >{{ruleGroup.name}}</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="category">
              <a-input v-model="ruleForm.category" placeholder="" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="extractRule">
              <a-input v-model="ruleForm.name" placeholder="" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="extractRule">
              <a-input v-model="ruleForm.logFeature" placeholder=" login" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="extractRule">
              <a-input v-model="ruleForm.extractRule" placeholder=" %{IPV4>>ip},%{DATA>>result}" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="switchRule">
              <a-input v-model="ruleForm.switchRule" placeholder=" ip:$getGeo>>country,city" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="replaceRule">
              <a-input v-model="ruleForm.replaceRule" placeholder=" result==succeed>>result=success" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="supplementRule">
              <a-input v-model="ruleForm.supplementRule" placeholder=":{type:http,opt:login}" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="12" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="description">
              <a-textarea v-model="ruleForm.description" placeholder="" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="isEnabled">
              <a-switch v-model="ruleForm.isEnabled"></a-switch>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
        <a-button type="link" ghost @click="handleCancel"></a-button>
        <a-button type="primary" @click="handleOk"></a-button>
      </template>
    </a-modal>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  export default {
    name: "rule-modal",
    props: {
      // 
      ruleVisible: {
        type: Boolean,
        default: false,
        required: true,
      },
      // 
      title: {
        type: String,
        default: "/",
      },
      //  
      content: {
        type: Object,
        default: function () {
          return {
            name: "",
            code:"",
            objectId:"",
            description: "",
            creator: "",
            gmtCreate: "",
            gmtModified: "",
          };
        },
      },
      //  0 1
      optType:{
        type:Number,
        default:0,
        required: true,
      }
    },
    data(){
      return{
        ruleForm: {
          name: "",
          ruleGroupId: "",
          category: "",
          logFeature: "",
          extractRule: "",
          switchRule: "",
          replaceRule: "",
          supplementRule: "",
          description: "",
          isEnabled: true,
        },
        ruleFormRule: {
          name: [{ required: true, message: "",trigger:'change' }],
          ruleGroupId: [{ required: true, message: "",trigger:'change' }],
          category: [{ required: true, message: "",trigger:'blur' }],
          // logFeature: [{ required: true, message: "",trigger:'blur' }],
          extractRule: [{ required: true, message: "",trigger:'blur' }],
          // switchRule: [{ required: true, message: "",trigger:'blur' }],
          // replaceRule: [{ required: true, message: "" ,trigger:'blur'}],
          // supplementRule: [{ required: true, message: "",trigger:'blur' }],
        },
      }
    },
    computed: {
      ...mapState(["ruleGroupData"]),
    },
    methods:{
      handleOk() {
        console.log(this.ruleForm)
        this.$refs.ruleForm.validate(valid => {
          if(valid){
            this.$emit("ruleOk", {formContent:this.ruleForm,optType:this.optType});
          } else {
            this.$message.warning('');
            return false
          }
        })
      },
      handleCancel() {
        this.$emit("ruleCancel", "");
      },
    },
    watch:{
      content:{
        handler:function(newVal){
          Object.assign(this.ruleForm,newVal);
          this.ruleForm.isEnabled = !!this.ruleForm.isEnabled;
        },
        deep:true
      },
      ruleVisible:{
        handler:function(newVal){
          if(newVal){
            this.$store.dispatch("getRuleGroupData", this);
          }
        }
      }
    },
    // created(){
    //   this.$API.searchRuleGroup({}).then(res => {
    //     if(res.code === 1000){
    //       this.ruleForm = Object.assign({},res.data,{isEnabled:this.ruleForm.isEnabled?true:false});
    //     }
    //   })
    // }
  }
</script>

