<template>
  <div>
    <a-form-model v-model="rule_template" ref="rule_template" :model="rule_template" :rules="ruleFormRule">

      <a-form-model-item label="">
        <a-input placeholder=" login" v-model="rule_template.logFeature" @change="formChange"/>
      </a-form-model-item>
      <a-form-model-item label="" prop="logData">
        <a-input placeholder="" v-model="rule_template.logData" @change="formChange"/>
      </a-form-model-item>
      <a-form-model-item label="">
        <a-input placeholder=" %{IPV4>>ip},%{DATA>>result}" v-model="rule_template.extractRule" @change="formChange"/>
      </a-form-model-item>

      <a-form-model-item label="">
        <a-input placeholder=" ip:$getGeo>>country,city" v-model="rule_template.switchRule" @change="formChange"/>
      </a-form-model-item>

      <a-form-model-item label="">
        <a-input placeholder=" result==succeed>>result=success" v-model="rule_template.replaceRule" @change="formChange"/>
      </a-form-model-item>

      <a-form-model-item label="">
        <a-input placeholder=":{type:http,opt:login}" v-model="rule_template.supplementRule" @change="formChange"/>
      </a-form-model-item>

    </a-form-model>
  </div>
</template>

<script>

  export default {
    name: "ruleForm",
    components: {},
    props: {
      basic_rules: {
        type: Object,
        default:()=>{}
      },
      proId: {
        type: String
      },
      tabId: {
        type: String
      },
    },
    data() {
      return {
        rule_template: {},
        ruleFormRule: {
              logData: [{ required: true, message: "",trigger:'blur' }]
          }
      }
    },

    watch: {
     basic_rules: {
        immediate:true,
        handler: function (val) {
          console.log(val)
          this.rule_template = Object.assign({},val);
        },
        deep: true
      }
    },
    methods: {
      formChange(){
        this.$emit('formChange',{data:this.rule_template,proId:this.proId,tabId:this.tabId})
      }
      },
    mounted() {
      // this.switch_props(this.basic_rules)
    }
  }
</script>

