<template>
  <div class="ruleGroup-modal">
    <a-modal
      :title="title"
      :visible="ruleGroupVisible"
      @cancel="handleCancel"
      cancelText=""
      okText=""
      :mask="false"
      :maskClosable="false"
      :width="600"
      class="modalCls"
    >
      <a-form-model ref="ruleGroupForm" :model="ruleGroupForm" :rules="ruleGroupFormRule">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="name">
              <a-input v-model="ruleGroupForm.name" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="code">
              <a-input v-model="ruleGroupForm.code" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="fileRegex">
              <a-input v-model="ruleGroupForm.fileRegex" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="fileEncode">
              <a-select v-model="ruleGroupForm.fileEncode"  placeholder="">
                <a-select-option value="utf-8">utf-8</a-select-option>
                <a-select-option value="gbk">gbk</a-select-option>
                <a-select-option value="utf-16">utf-16</a-select-option>
                <a-select-option value="unicode">unicode</a-select-option>
                <a-select-option value="latin1">latin1</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="objectId">
              <a-select v-model="ruleGroupForm.objectId" placeholder="" allowClear>
                  <a-select-option :value="obj.id" v-for="obj of objectData" :key="obj.id">{{obj.objectName}}</a-select-option>
                </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="description">
              <a-textarea v-model="ruleGroupForm.description" placeholder="" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
          <a-button type="link" ghost @click="handleCancel"></a-button>
          <a-button type="primary" @click="handleOk"></a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "ruleGroup-modal",
  props: {
    // 
    ruleGroupVisible: {
      type: Boolean,
      default: false,
      required: true,
    },
    // 
    title: {
      type: String,
      default: "/",
    },
    //  
    content: {
      type: Object,
      default: function () {
        return {
          name: "",
          code:"",
          objectId:"",
          description: "",
          creator: "",
          gmtCreate: "",
          gmtModified: "",
        };
      },
    },
    //  0 1
    optType:{
      type:Number,
      default:0,
      required: true,
    }
  },
  data() {
    return {
      ruleGroupFormRule: {
        name: [{ required: true, message: "",trigger:'blur' }],
        code: [{ required: true, message: "",trigger:'blur' }],
        fileRegex: [{ required: true, message: "",trigger:'blur' }],
        fileEncode: [{ required: true, message: "",trigger:'blur' }],
        objectId: [{ required: true, message: "",trigger:'change' }],
      },
      ruleGroupForm:{
          name: "",
          code:"",
          fileRegex:"",
          fileEncode:"",
          objectId:"",
          description: "",
      }
    };
  },
  computed: {
    ...mapState(["objectData"]),
  },
  methods: {
    handleOk() {
        this.$refs.ruleGroupForm.validate(valid => {
            if(valid){
                this.$emit("ruleGroupOk", {formContent:this.ruleGroupForm,optType:this.optType});
            } else {
                this.$message.warning('');
                return false
            }
        })
    },
    handleCancel() {
      this.$emit("ruleGroupCancel", "");
    },
  },
  watch:{
    content:{
      handler:function(newVal){
        Object.assign(this.ruleGroupForm,newVal);
      },
      deep:true
    },
    ruleGroupVisible:{
      handler:function(newVal){
        if(newVal){
          this.$store.dispatch("getObjectData", this);
        }
      }
    }
  },
};
</script>
<style>

</style>