<template>
  <div class="unit-modal">
    <a-modal
      :title="title"
      :visible="unitVisible"
      @cancel="handleCancel"
      cancelText=""
      okText=""
      :mask="false"
      :maskClosable="false"
      :width="650"
      class="modalCls"
    >
      <a-form-model ref="unitForm" :model="unitForm" :rules="unitFormRule" style="margin-bottom:20px">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="cname">
              <a-input v-model="unitForm.cname" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="ename">
              <a-input v-model="unitForm.ename" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="">
              <a-input v-model="unitForm.city" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="">
              <a-input v-model="unitForm.contact" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="">
              <a-input v-model="unitForm.telephone" placeholder="" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="" prop="description">
              <a-textarea v-model="unitForm.description" placeholder="" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
          <a-button type="link" ghost @click="handleCancel"></a-button>
          <a-button type="primary" @click="handleOk"></a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
export default {
  name: "unit-modal",
  props: {
    // 
    unitVisible: {
      type: Boolean,
      default: false,
      required: true,
    },
    // 
    title: {
      type: String,
      default: "/",
    },
    //  
    content: {
      type: Object,
      default: function () {
        return {
          cname: "",
          ename: "",
          city: "",
          contact: "",
          telephone: "",
          description: "",
          creator: "",
          createTime: "",
          updateTime: "",
        };
      },
    },
    //  0 1
    optType:{
      type:Number,
      default:0,
      required: true,
    }
  },
  data() {
    return {
      unitFormRule: {
        cname: [{ required: true, message: "",trigger:'blur'}],
        ename: [{ required: true, message: "",trigger:'blur' }],
        city: [{ required: true, message: "",trigger:'blur' }],
        contact: [{ required: true, message: "",trigger:'blur' }],
        telephone: [{ required: true, message: "",trigger:'blur' }],
      },
      unitForm:{
          cname: "",
          ename: "",
          city: "",
          contact: "",
          telephone: "",
          description: "",
      }
    };
  },
  methods: {
    handleOk() {
        this.$refs.unitForm.validate(valid => {
            if(valid){
                this.$emit("unitOk", {formContent:this.unitForm,optType:this.optType});
            } else {
                this.$message.warning('');
                return false
            }
        })
    },
    handleCancel() {
      this.$emit("unitCancel", "");
    },
  },
  watch:{
    content:{
      handler:function(newVal){
        Object.assign(this.unitForm,newVal);
      },
      deep:true
    }
  }
};
</script>
